  methods: {
    init() {
      const totalHeight = this.$refs.whtable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      this.timer = setInterval(() => {
        if (cHeight - totalHeight - 200 > this.num) {
          this.isOverShow();
          clearInterval(this.timer);
        }
        this.num--;
        this.style = { top: `${this.num}px` };
      }, 25);
    },
    handleMouseEnter() {
      const totalHeight = this.$refs.whtable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      if (cHeight - totalHeight > this.num) return;
      clearInterval(this.timer);
    },
    handleMouseOut() {
      const totalHeight = this.$refs.whtable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      if (cHeight - totalHeight - 200 > this.num) return;
      this.init();
    },
    isOverShow() {
      this.$emit('overShow');
    }
  }
};
