    text-align: center;
    border-bottom: 1px solid #431b24;
    background-color: #f8f5f1;
    span {
      font-size: 20px;
      width: 25%;
      line-height: 50px;
      display: inline-block;
    }
  }
  &-content {
    position: absolute;
    top: 50px;
    width: 100%;
    z-index: 1;
    cursor: pointer;
    li {
      width: 100%;
      line-height: 50px;
      text-align: center;
      border-bottom: 1px solid #e0dede;
      span {
        font-size: 18px;
        line-height: 50px;
        width: 25%;
        color: #2b2b2b;
        display: inline-block;
      }
      .red {
        color: #7d0000;
      }
    }
  }
}
</style>
