<template>
  <div ref="table"
       class="gytable">
    <div class="gytable-title">
      <span>代码</span>
      <span>简称</span>
      <span>挂牌日期</span>
      <span>成立日期</span>
    </div>
    <ul ref="gytable"
        class="gytable-content"
        :style="style"
        @mouseenter="handleMouseEnter"
        @mouseleave="handleMouseOut">
      <li v-for="item in data"
          :key="item.id">
        <span>{{item.id}}</span>
        <span>{{item.title}}</span>
        <span class="red">{{item.date}}</span>
        <span class="red">{{item.date1}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'gytable',
  data() {
    return {
      style: 'top: 50px',
      timer: undefined,
      num: 50,
      data: [
        {
          id: 'GY8118',
          title: '湖北南财文化',
          date: '2018-09-19',
          date1: '2011-05-11'
        },
        {
          id: 'GY8117',
          title: '北京中环智学（ 预）',
          date: '2018-08-29',
          date1: '2013-10-28'
        },
        {
          id: 'GY012010-000015B、GY012010-0000',
          title: '深圳艺能数字影视',
          date: '2010-07-23',
          date1: '2001-03-19'
        },
        {
          id: 'GY8116',
          title: '周口中环国投热力',
          date: '2018-03-27',
          date1: '2016-05-27'
        },
        {
          id: 'GY8115',
          title: '中环国投环保研究院',
          date: '2018-03-27',
          date1: '2016-05-18'
        },
        {
          id: 'GY8114',
          title: '中环国投（ 蒙阴）',
          date: '2018-03-27',
          date1: '2016-07-25'
        },
        {
          id: 'GY8113',
          title: '中广闪动（ 北京）',
          date: '2018-03-09',
          date1: '2014-04-29'
        },
        {
          id: 'GY7112',
          title: '新华文博物业',
          date: '2017-09-29',
          date1: '1992-10-06'
        },
        {
          id: 'GY7111',
          title: '新华数创传媒',
          date: '2017-09-29',
          date1: '2009-09-10'
        },
        {
          id: 'GY7110',
          title: '北京一片红集邮文化中心',
          date: '2017-09-07',
          date1: '1993-03-19'
        },
        {
          id: 'GY6108',
          title: '南京安运',
          date: '2017-05-5',
          date1: '2014-03-14'
        },
        {
          id: 'GY7201',
          title: '书城文化投资',
          date: '2017-03-17',
          date1: '2002-04-29'
        },
        {
          id: 'GY7101',
          title: '明博教育科技',
          date: '2017-04-05',
          date1: '2009-12-08'
        },
        {
          id: 'GY5106',
          title: '华夏盛轩',
          date: '2015-11-7',
          date1: '2008-08-18'
        },
        {
          id: 'GY4105',
          title: '中地华安',
          date: '2014-12-6',
          date1: '2011-09-28'
        },
        {
          id: 'GY6107',
          title: '晒晒乐',
          date: '2016-02-01 ',
          date1: '2009-11-12'
        },
        {
          id: 'GY012014-00170J',
          title: '书城投控',
          date: '2014-12-5',
          date1: '2002-04-29'
        },
        {
          id: 'G6201',
          title: 'CUTV',
          date: '2016-11-17',
          date1: '2011-02-14'
        }
      ]
    };
  },
  mounted() {
    // height = 600px
    this.init();
  },
  methods: {
    init() {
      const totalHeight = this.$refs.gytable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      this.timer = setInterval(() => {
        if (cHeight - totalHeight - 200 > this.num) {
          this.isOverShow();
          clearInterval(this.timer);
        }
        this.num--;
        this.style = { top: `${this.num}px` };
      }, 25);
    },
    handleMouseEnter() {
      const totalHeight = this.$refs.gytable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      if (cHeight - totalHeight - 200 > this.num) return;
      clearInterval(this.timer);
    },
    handleMouseOut() {
      const totalHeight = this.$refs.gytable.clientHeight;
      const cHeight = this.$refs.table.clientHeight;
      if (cHeight - totalHeight > this.num) return;
      this.init();
    },
    isOverShow() {
      this.$emit('overShow');
    }
  }
};
</script>

<style lang='less' scoped>
.gytable {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding-top: 50px;
  height: 100%;
  min-height: 650px;
  &-title {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    z-index: 10;
    text-align: center;
    border-bottom: 1px solid #431b24;
    background-color: #f8f5f1;
    span {
      font-size: 20px;
      width: 25%;
      line-height: 50px;
      display: inline-block;
    }
  }
  &-content {
    position: absolute;
    top: 50px;
    width: 100%;
    z-index: 1;
    cursor: pointer;
    li {
      width: 100%;
      line-height: 50px;
      text-align: center;
      border-bottom: 1px solid #e0dede;
      span {
        font-size: 18px;
        line-height: 50px;
        width: 25%;
        color: #2b2b2b;
        display: inline-block;
      }
      .red {
        color: #7d0000;
      }
    }
  }
}
</style>
